/**
 * route list
 */

export const loginRoute = {
    key: 'login',
    name: '',
    path: '/login'
}

export const personalRoute = {
    key: 'personal',
    name: '',
    path: '/personal',
    child: {
        base: {
            key: 'base',
            name: '',
            path: '/personal/base'
        }
    }
}

export default { loginRoute, personalRoute };



